import goodBox from "../../components/common/goodBox.vue"
export default {
	components: {
		goodBox
	},
	data() {
		return {
			brandData: null,
			goodsList: []
		};
	},
	methods: {
		getBrandDetail(id) {
			this.$api.getManufacturers(id).then(res => {
				// console.log(res);
				this.brandData = res.data.data
				this.goodsList = res.data.goodsList
			})
		}
	},
	onLoad(options) {
		this.getBrandDetail(options.id)
	}
}
